<template>
  <div class="addAddress mescroll">
    <div class="list">
      <div class="item flex">
        <div class="label">姓名： </div>
        <div class="value flex-2">
          <div>
             {{this.name}}
          </div> 
        </div>
      </div>
      <div class="item item-center flex">
        <div class="label">手机号：</div>
        <div class="value flex-2">
           {{this.telno}}
        </div>
      </div>
      <div class="item item-center flex">
        <div class="label">验证码</div>
        <div class="value flex-2">
           <p>
            <input class="checkcode" type="number" v-model="checkcode" placeholder="请输入验证码"  @blur="blur" />
          </p> 
            <div class="btn2" @click="submitsendcheckcode">获取验证码<span v-if="min">({{min}})</span></div> 
        </div>
      </div>  
      <div class="item item-bottom flex">
        <div class="label">设为默认账号</div>
        <div class="value flex-2">
          <van-switch v-model="checked" active-color="#eb5252" bind:change="onChange" />
        </div>
      </div>
    </div>
    <div class="btnWrap">
      <div class="btn" @click="save"> 绑 定 </div>
    </div>
  </div>
</template>
<script>
import { Switch, Field, Popup, Area,Checkbox } from "vant";
import { getUserInfo, createAuthVip,sendcheckcode2 } from "@/api"; 
import myUserlistVue from './myUserlist.vue';
export default {
  components: {
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Area.name]: Area,
    [Checkbox.name]: Checkbox
  },
  data() {
    return {
      id: "",
      name: "",
      telno: "",
      checked: false,
      checkcode: "",
      min: 0    
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    submitsendcheckcode(){
        if (this.min) {
          return;
        }
        this.$toast.allowMultiple();
        const loading = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true // 禁用背景点击
        }); 
        sendcheckcode2({
          phoneNumber: this.telno,
          username: this.name
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("验证码发送成功，请在您的手机短信中查看验证码");
            this.min = 60;
            this.Interval = setInterval(() => {
              this.min--;
              if (this.min < 1) {
                clearInterval(this.Interval);
              }
            }, 1000); 
          }
          else{
            this.$toast(res.msg);
          }
        });
    },
    save() {
      if (!this.name) {
        this.$toast("请输入姓名");
        return;
      }
      if (!this.telno) {
        this.$toast("请输入手机号");
        return;
      }
      if(!this.checkcode)
      {
        this.$toast("请输入验证码");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      createAuthVip({
        usercode: this.id || undefined, 
        isDefault: this.checked ? 1 : 0,
        checkcode: this.checkcode
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast("绑定成功");
          setTimeout(() => {
            this.$router.push({name: "myUserlist"});
          });
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      getUserInfo({ usercode: this.$route.query.id }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.id = res.data.userCode;
          this.name = res.data.userName;
          this.telno = res.data.userMobile;
          this.checked = res.data.isDefault == 1 ? true : false; 
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    document.title =  "绑定账号资料";
    if (this.$route.query.id) {
      this.id= this.$route.query.id;
      this.loadData();
    }
  }
};
</script>
<style lang="scss" scoped>
.addAddress {
  background: #fff;
  .list {
    padding: 0 15px;
    .flex {
      display: flex;
    }
    .item {
      padding: 15px 0;
      border-bottom: 1pt solid #efefef;
      .flex-2 {
        flex: 2;
      }

      .label {
        font-size: 15px;
        line-height: 30px;
        font-weight: bold;
        margin-right: 15px;
        width: 60px;
      }
      .value {
        input {
          height: 30px;
          font-size: 15px;
          color: #666;
          width: 100%;
        }
        textarea {
          width: 100%;
          height: 30px;
          font-size: 15px;
          color: #666;
          margin-top: 7px;
        }
      }

      .value {
        .prompt {
          color: #999;
          font-size: 12px;
          line-height: 20px;
          span {
            color: $base-color;
          }
        }
      }
    }
    .area {
      .value {
        font-size: 15px;
        color: #666;
      }
      img {
        width: 5px;
      }
    }
    .item-center {
      align-items: center;
    }
    .item-bottom {
      .label {
        width: 100px;
        line-height: 37px;
      }
      .value {
        text-align: right;
        margin: 3.5px 0 0;
      }
    }
  }
  .btnWrap {
    background: #fff;
    margin: 20px 0 0;
    width: calc(100% - 30px);
    padding: 0 15px 10px;
    .btn {
      background: $base-color;
      color: #fff;
      font-size: 18px;
      width: 100%;
      text-align: center;
      height: 50px;
      line-height: 50px;
      border-radius: 5px;
    }
  }
}
</style>
<style lang="scss">
.addAddress {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell {
    padding: 5px 0;
  }
}
input.checkcode{
        border: 0;
        width: 100%;
        height: 20px;
        font-size: 20px;
        line-height: 20px;
        color: #999;
    }
    .btn2 {
      width: 100px;
      display:inline-block;
      background: #ef5c54;
      color: #fff;
      text-align: center;
      height: 30px;
      line-height: 30px;
      font-size: 14px;
      border-radius: 5px;
      margin: 10px 0 0; 
    }
</style>
